as y estacionamientos.")])]),_c('div',{staticClass:"col-md-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/2/3.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('div',{staticClass:"tarjeta rounded-circle color-primario d-inline-block p-3 mb-4"},[_c('h4',{staticClass:"text-white mb-0 mx-1"},[_vm._v("2")])]),_c('p',[_vm._v("El vehículo recolector de residuos debe tener superficies internas lisas de bordes redondeados de forma que se facilite el aseo y estar provisto de ventilación adecuada. Dotado de un sistema de carga y descarga que no permita que se rompan los recipientes. Si es de carga manual, la altura desde el piso al punto de carga en el vehículo debe ser inferior a 1.20 m.")])]),_c('div',{staticClass:"col-md-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/2/4.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-md-5 mb-4 mb-md-0"},[_c('div',{staticClass:"tarjeta rounded-circle color-primario d-inline-block p-3 mb-4"},[_c('h4',{staticClass:"text-white mb-0 mx-1"},[_vm._v("3")])]),_c('p',[_vm._v("El vehículo estará dotado de canastillas retornables donde se depositan las bolsas con residuos, estos recipientes serán de material rígido e impermeable, evitando la compresión de los residuos al sobreponer bolsas. Los vehículos de recolección se lavarán y desinfectarán de manera apropiada únicamente en los lugares designados para tal fin dentro de las instalaciones del prestador del servicio especial de aseo, y el efluente proveniente del lavado y desinfección del vehículo debe ser objeto de tratamiento cumpliendo con lo establecido en el Decreto 1594 de 1984 o la norma que lo sustituya o modifique.")])]),_c('div',{staticClass:"col-md-7"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/2/5.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{static